.Modal {
  position: fixed;
  top: 2.5%;
  left: 7.5%;
  width: 85%;
  height: 95%;
  z-index: 3;
  background-color: var(--primaryColor);
  overflow-y: auto;
  border: 1px solid;
  border-color: var(--borderColor);
  border-radius: 1vh;
}

.Modal-logo {
  max-height: 15vh;
  margin-top: 3vh;
}

.Modal-title {
  margin: 3vh;
  margin-bottom: 1.5vh;
}

.Modal-description {
  margin: 1.5vh;
  margin-bottom: 5vh;
}

.Modal-features {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 3vh;
  margin-top: 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: var(--borderColor);
  text-align: left;
}

.Modal-features-column {
  align-self: stretch;
  max-width: 30vh;
  padding: 3vh;
}

.Modal-features-column2 {
  margin-left: 10vh;
  margin-right: 10vh;
}

.Modal-features-title {
  padding-bottom: 1vh;
}

.Modal-usage {
  margin-left: 2vh;
  margin-right: 2vh;
  padding-left: 3vh;
  padding-right: 3vh;
  padding-top: 0;
}

.Modal-usage-title {
  padding-bottom: 1vh;
}

.Modal-usage-paragraph {
  padding-bottom: 3vh;
  padding-right: 15vw;
  padding-left: 15vw;
}

.Modal-usage-code-block {
  background-color: #2d2d2d;
  color: var(--textColor);
  padding: 0.2vh;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  word-break: break-all;
  border-radius: 0.25vh;
}

.Modal-url-input {
  margin-bottom: 3vh;
}

.Modal-url-input-field {
  background-color: var(--primaryColor);
  max-width: 95%;
  border: 1px solid;
  border-color: var(--borderColor);
  border-radius: 1vh;
  font-size: x-large;
  font-weight: bold;
  color: var(--textColor);
}

.Modal-url-validation {
  padding-top: 1vh;
}

.Modal-url-button {
  margin: 0;
}

.Modal-close-button {
  background-color: var(--primaryColor);
  border: 0px solid;
  font-size: 5vh;
  color: var(--textColor);
}

.Modal-overlay {
  display: none;
}

.Modal-overlayShow {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

@media screen and (max-width: 1225px) {
  .Modal-close-button {
    margin-bottom: 3vh;
  }
  .Modal-usage-paragraph {
    padding-right: 7.5vw;
    padding-left: 7.5vw;
  }
}

@media screen and (max-width: 1000px) {
  .Modal-logo {
    max-height: 50vw;
  }
  .Modal-features {
    flex-direction: column;
    text-align: center;
  }
  .Modal-features-column {
    align-self: auto;
  }
  .Modal-features-column2 {
    margin-left: 0;
    margin-right: 0;
  }
  .Modal-usage-paragraph {
    padding-right: 0;
    padding-left: 0;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .Modal-logo {
    animation: Modal-logo-spin infinite 10s linear;
  }
}

@keyframes Modal-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
